import React, { useState } from 'react';

export default function AddOptions({ onAdd }) {
  const [text, setText] = useState('');
  return (
    <form
      className='add-form'
      onSubmit={e => {
        e.preventDefault();

        if (text.trim().length !== 0) {
          onAdd(text);
          setText('');
        } else {
          window.alert('  .');
        }
      }}>
      <input
        type='text'
        value={text}
        onChange={e => {
          const value = e.target.value;

          if (value.length > 16) {
            alert('    .');
            return;
          }

          setText(value);
        }}
      />
      <button></button>
    </form>
  );
}
